<script>
export default {
    name:'PlatformView',
    methods: {
	  	handleScroll() {
	  		let top = pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;

	  	},
	  	handleAnimate() {
	  		let top = pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;//获取滚动条
	  		let vh = document.documentElement.clientHeight;//获取视口大小
            let dom = document.querySelectorAll(".animate");//获取dom元素
        //[].slice.call(dom)能将具有length属性的对象转成数组
	  		[].slice.call(dom).forEach(v => {
          //console.log(v);
          
	  		if(top + vh > v.offsetTop){
            var delay = v.dataset.delay;
            console.log(v.classList);
            
	  				if(delay){
	  					setTimeout(() => {
	  						v.style.opacity = 1;
		  					v.classList.add(v.dataset.ani)
		  				}, delay)
	  				}else{
	  					v.style.opacity = 1;
	  					v.classList.add(v.dataset.ani)
	  				}
	  			}else{
	  				v.classList.remove(v.dataset.ani)
	  				v.style.opacity = 0;
	  			}
	  		})
	  	}
	  },
	  mounted() {
	  	 this.$nextTick(() => {
	  		//this.handleAnimate()//初始化第一次加载时在视口内就执行动画
	  		 addEventListener('scroll', this.handleScroll);
	  		 addEventListener('scroll', this.handleAnimate);
	  	})
	  	
	  },
	  destroyed() {
	  	removeEventListener('scroll', this.handleScroll);//避免影响其他页面
	  	removeEventListener('scroll', this.handleAnimate);
	  }
}
</script>

