import {request} from "./request"
//1.请求首页轮播图
export function getHomeSwiperImg(lang){
  return request({
    url:'/main/websiteInfo/homePicture',
	params:{
		is_picture: 1,
		language:lang
	}
  })
}
//1.请求首页logo图
export function getHomeGoods(type,page){
  return request({
    url:'/home/data',
    params:{
      type,
      page
    }
  })
}