<template>
  <div class="block">
    <el-carousel height="750px">
      <el-carousel-item v-for="item in imgSrcs" :key="item.index">
        <a href="">
          <img :src="item" alt="">
        </a>
      </el-carousel-item>
    </el-carousel>
  </div>
</template>
<script>
import eventHub from '../../eventHub/eventHub.js'
import {getHomeSwiperImg} from 'network/home'
export default {
    name:'Swiper',
	data(){
		return{
			imgSrcs:[]
		};
	},
	methods:{
		getHomeSwiperImg(lang){
			getHomeSwiperImg(lang).then(data => {
				debugger;
				this.imgSrcs = data.obj;
				console.log(this.imgSrcs);
			});
		}
	},
	mounted:function(){
		this.getHomeSwiperImg(this.$i18n.locale);
		//监听切换语言事件 
		eventHub.$on("change-swiper", lang =>{
			this.getHomeSwiperImg(lang);
		});
	}
}
</script>

<style>
img{
  width: 100%;
  height: 100%;
}
.el-carousel__indicators--horizontal {
    bottom: 198px;
    left: 20%;
}
.el-carousel__indicator--horizontal {
    padding: 12px 8px;
}

.el-carousel__button{
  width: 34px;
  height: 3px;
  background-color: #8ccb22;
}
</style>